"use client"

import { useState } from "react"
import { MoreVertical, ShoppingCart, BarChart3, DollarSign, Clock, Plus } from "lucide-react"
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"

const dataSets = {
  orders: [
    { category: "Jan", value: 28000 },
    { category: "Feb", value: 35000 },
    { category: "Mar", value: 32000 },
    { category: "Apr", value: 41000 },
    { category: "May", value: 38000 },
  ],
  sales: [
    { category: "Jan", value: 45000 },
    { category: "Feb", value: 52000 },
    { category: "Mar", value: 48000 },
    { category: "Apr", value: 61000 },
    { category: "May", value: 58000 },
  ],
  profit: [
    { category: "Jan", value: 38000 },
    { category: "Feb", value: 42000 },
    { category: "Mar", value: 40000 },
    { category: "Apr", value: 51000 },
    { category: "May", value: 48000 },
  ],
  income: [
    { category: "Jan", value: 30000 },
    { category: "Feb", value: 35000 },
    { category: "Mar", value: 32000 },
    { category: "Apr", value: 42000 },
    { category: "May", value: 39000 },
  ],
}

export function EarningReports() {
  const [activeTab, setActiveTab] = useState("orders")
  const chartData = dataSets[activeTab as keyof typeof dataSets]

  const tabs = [
    { id: "orders", label: "Orders", icon: ShoppingCart },
    { id: "sales", label: "Sales", icon: BarChart3 },
    { id: "profit", label: "Profit", icon: DollarSign },
    { id: "income", label: "Income", icon: Clock },
  ]

  return (
    <Card className="lg:col-span-2">
      <CardHeader className="flex flex-col sm:flex-row sm:items-center sm:justify-between pb-4">
        <div>
          <CardTitle>Earning Reports</CardTitle>
          <CardDescription>Yearly Earnings Overview</CardDescription>
        </div>
        <button className="mt-2 sm:mt-0 text-gray-400 dark:text-white hover:text-gray-600 transition-colors">
          <MoreVertical size={20} />
        </button>
      </CardHeader>

      <CardContent className="space-y-4">
        <div className="flex gap-2 pb-4 border-b border-gray-100 dark:border-gray-800 overflow-x-auto">
          {tabs.map((tab) => {
            const IconComponent = tab.icon
            const isActive = activeTab === tab.id

            return (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id)}
                className={`px-3 sm:px-4 py-2 rounded-lg border-2 font-medium text-xs sm:text-sm flex items-center gap-2 whitespace-nowrap transition-colors ${
                  isActive
                    ? "border-purple-500 text-purple-600 dark:text-purple-400 bg-purple-50 dark:bg-purple-950"
                    : "border-gray-200 dark:border-gray-700 text-gray-600 dark:text-white hover:bg-gray-50 dark:hover:bg-gray-900"
                }`}
              >
                <IconComponent size={16} className="sm:w-5 sm:h-5" />
                {tab.label}
              </button>
            )
          })}
          <button className="px-3 sm:px-4 py-2 rounded-lg border border-gray-200 dark:border-gray-700 text-gray-600 dark:text-white font-medium text-xs sm:text-sm flex items-center justify-center whitespace-nowrap hover:bg-gray-50 dark:hover:bg-gray-900 transition-colors">
            <Plus size={16} className="sm:w-5 sm:h-5" />
          </button>
        </div>

        <ResponsiveContainer width="100%" height={250} className="sm:h-80">
          <BarChart data={chartData} margin={{ top: 20, right: 30, left: -20, bottom: 20 }}>
            <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" vertical={false} />
            <XAxis dataKey="category" stroke="#9ca3af" style={{ fontSize: "12px" }} />
            <YAxis stroke="#9ca3af" style={{ fontSize: "12px" }} />
            <Tooltip contentStyle={{ backgroundColor: "#fff", border: "1px solid #e5e7eb", borderRadius: "8px" }} />
            <Bar dataKey="value" fill="#8b5cf6" radius={[8, 8, 0, 0]} />
          </BarChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  )
}
