'use client'

import { MoreVertical, DollarSign } from 'lucide-react'
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts'

export function ProjectStatus() {
  const data = [
    { month: 'Jan', value: 2000 },
    { month: 'Feb', value: 3000 },
    { month: 'Mar', value: 2500 },
    { month: 'Apr', value: 3500 },
    { month: 'May', value: 3200 },
    { month: 'Jun', value: 4000 },
  ]

  return (
    <div className="bg-white dark:bg-gray-950 border-b border-slate-700  rounded-lg p-6 shadow-sm ">
      <div className="flex justify-between items-start mb-6">
        <h3 className="text-gray-800 dark:text-white font-semibold text-lg">Project Status</h3>
        <button className="text-gray-400 dark:text-white hover:text-gray-600">
          <MoreVertical size={20} />
        </button>
      </div>

      <div className="mb-6">
        <div className="flex items-baseline gap-2 mb-1">
          <span className="text-2xl font-bold text-gray-800 dark:text-white">$4,3742</span>
          <span className="text-green-500 text-sm font-medium">+10.2%</span>
        </div>
        <p className="text-gray-500 dark:text-white text-sm">Your Earnings</p>
      </div>

      <ResponsiveContainer width="100%" height={200}>
        <LineChart data={data} margin={{ top: 5, right: 10, left: -20, bottom: 5 }}>
          <CartesianGrid strokeDasharray="3 3" stroke="#E5E7EB" vertical={false} />
          <XAxis dataKey="month" stroke="#999" style={{ fontSize: '12px' }} />
          <YAxis stroke="#999" style={{ fontSize: '12px' }} />
          <Tooltip />
          <Line type="monotone" dataKey="value" stroke="#FFA500" strokeWidth={2} dot={false} />
        </LineChart>
      </ResponsiveContainer>

      <div className="grid grid-cols-2 gap-4 mt-6 pt-4 border-t">
        <div>
          <div className="flex items-center gap-2 mb-1">
            <DollarSign size={16} className="text-gray-400 dark:text-white" />
            <span className="text-gray-700 dark:text-white font-medium">Donates</span>
          </div>
          <div className="flex items-baseline gap-2">
            <span className="text-gray-800 dark:text-white font-semibold">$756.26</span>
            <span className="text-red-500 text-sm">-139.34</span>
          </div>
        </div>
        <div>
          <div className="flex items-center gap-2 mb-1">
            <DollarSign size={16} className="text-gray-400 dark:text-white" />
            <span className="text-gray-700 dark:text-white font-medium">Podcasts</span>
          </div>
          <div className="flex items-baseline gap-2">
            <span className="text-gray-800 dark:text-white font-semibold">$2,207.03</span>
            <span className="text-green-500 text-sm">+576.24</span>
          </div>
        </div>
      </div>
    </div>
  )
}
