'use client'

import { MoreVertical } from 'lucide-react'
import { PieChart, Pie, Cell, ResponsiveContainer } from 'recharts'

export function BrowserStates() {
  const browsers = [
    { name: 'Google Chrome', percentage: 54.4, color: '#EA4335' },
    { name: 'Apple Safari', percentage: 14.6, color: '#0071E3' },
    { name: 'Mozilla Firefox', percentage: 6.1, color: '#FF7139' },
    { name: 'Opera Mini', percentage: 8.0, color: '#FF1B2D' },
    { name: 'Internet Explorer', percentage: 4.2, color: '#00A4EF' },
    { name: 'Brave', percentage: 0.3, color: '#FB542B' },
  ]

  const chartData = browsers.map(b => ({ value: b.percentage, name: b.name, color: b.color }))

  return (
    <div className="bg-white dark:bg-gray-950 border-b border-slate-700 rounded-lg  p-6 shadow-sm h-full">
      <div className="flex justify-between items-start mb-6">
        <div>
          <h3 className="text-gray-800 dark:text-white font-semibold text-lg">Browser States</h3>
          <p className="text-gray-500 dark:text-white text-sm">Counter April 2022</p>
        </div>
        <button className="text-gray-400 dark:text-white hover:text-gray-600">
          <MoreVertical size={20} />
        </button>
      </div>

      <div className="space-y-4">
        {browsers.map((browser, index) => (
          <div key={index} className="flex items-center justify-between">
            <div className="flex items-center gap-3 flex-1">
              <div className="w-10 h-10 rounded-full flex items-center justify-center" style={{ backgroundColor: browser.color }}>
                <span className="text-white dark:text-black text-xs font-bold">
                  {browser.name.charAt(0)}
                </span>
              </div>
              <span className="text-gray-700 dark:text-white font-medium">{browser.name}</span>
            </div>
            <div className="flex items-center gap-4">
              <span className="text-gray-700 dark:text-white font-semibold">{browser.percentage}%</span>
              <ResponsiveContainer width={40} height={40}>
                <PieChart>
                  <Pie
                    data={[
                      { value: browser.percentage, fill: browser.color },
                      { value: 100 - browser.percentage, fill: '#E5E7EB' }
                    ]}
                    innerRadius={12}
                    outerRadius={18}
                    dataKey="value"
                    startAngle={90}
                    endAngle={450}
                  >
                    {[browser.color, '#E5E7EB'].map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry} />
                    ))}
                  </Pie>
                </PieChart>
              </ResponsiveContainer>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}
