'use client'

import { MoreVertical,Figma } from 'lucide-react'

export function ActiveProjects() {
  const projects = [
    { id: 1, name: 'Laravel', desc: 'Ecommerce', percentage: 85, color: '#DC3545' },
    { id: 2, name: 'Figma', desc: 'App UI Kit', percentage: 86, color: '#6366F1' },
    { id: 3, name: 'VueJS', desc: 'Calendar App', percentage: 90, color: '#10B981' },
    { id: 4, name: 'React', desc: 'Dashboard', percentage: 37, color: '#06B6D4' },
    { id: 5, name: 'Bootstrap', desc: 'Website', percentage: 22, color: '#8B5CF6' },
    { id: 6, name: 'Sketch', desc: 'Website Design', percentage: 29, color: '#F59E0B' },
  ]

  return (
    <div className="bg-white dark:bg-gray-950 border-b border-slate-700 rounded-lg  p-6 shadow-sm h-full">
      <div className="flex justify-between items-start mb-6">
        <div>
          <h3 className="text-gray-800 dark:text-white font-semibold text-lg">Active Projects</h3>
          <p className="text-gray-500 dark:text-white text-sm">Average 72% completed</p>
        </div>
        <button className="text-gray-400 dark:text-white hover:text-gray-600">
          <MoreVertical size={20} />
        </button>
      </div>

      <div className="space-y-4">
        {projects.map((project) => (
          <div key={project.id} className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div
                className="w-10 h-10 rounded flex items-center justify-center text-white dark:text-black font-bold text-sm"
                style={{ backgroundColor: project.color }}
              >
                {/* {project.name} */}
                {/* <{project.name}/> */}
                <Figma />
              </div>
              {/* <Figma /> */}
              <div>
                <p className="text-gray-800 dark:text-white font-medium">{project.name}</p>
                <p className="text-gray-500 dark:text-white text-xs">{project.desc}</p>
              </div>
            </div>
            <div className="flex items-center gap-3">
              <div className="w-24 h-2 bg-gray-200 dark:bg-black rounded-full overflow-hidden">
                <div
                  className="h-full rounded-full"
                  style={{ width: `${project.percentage}%`, backgroundColor: project.color }}
                />
              </div>
              <span className="text-gray-700 dark:text-white font-semibold text-sm w-8 text-right">{project.percentage}%</span>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}
